import React, { Component } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Login from "./login.js";
import UserService from "../../services/user.service.js";
import Helper from "Helpers/utility.helper.js";
import Modal from "./modal.component.js";
import { Set } from "Helpers/axios.helper.js";
import Link from "valuelink";

const EmailRegex = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
const initialState = {
  Email: "",
  Password: "",
  forgotEmail: "",
  Type: "",
  ShowforgotModal: false,
  ShowverificationModal: false,
  ShowAlertModal: false,
  Message: "",
  SuccessModal: false,
  Videos: "",
  checkBoxStatus: false,
  ExtraServices: {'a':""},
  ExtraWarehouseType: {"b":""},
  ExtraSecurity: {"c":""},
  Otherserviceone:"",
  Otherservicetwo:"",
};
let links = {};
let VidForForgetPwd;
export default class LoginContainer extends Component {
  constructor(props) {
    super();
    initialState.Type =
      props.location.pathname.indexOf("/findspace/login") > -1
        ? "Buyer"
        : "Owner";
    this.state = initialState;
    this.handleChange = this.handleChange.bind(this);
    this.login = this.login.bind(this);
  }
  componentWillMount() {
    if (localStorage.getItem("userinfo") != null) {
      let UserDetail = Helper.DecodeToken(localStorage.getItem("userinfo"));
      if (UserDetail.Type == "Buyer") {
        if (localStorage.getItem("loginDetail") !== null) {
          let detail = localStorage.getItem("loginDetail");
          this.props.history.push("/warehouse/find");
          this.setState({ email: JSON.parse(detail).email, password: JSON.parse(detail).password, checkBoxStatus: true })
        } else {
          this.setState({ email: '', password: '' });
          this.props.history.push("/warehouse/find");
          // console.log('detail2==');
        }
      } else {
        switch (UserDetail.Status) {
          case 1:
            this.props.history.push("/warehouse/list");
            break;
          case 3:
            this.props.history.push("/warehouse/list");
            break;
        }
      }
    }

    UserService.GetVideos().then(response => {

      if (this.props.location.pathname == "/warehouse/login") {
        this.setState({ Videos: response.data[1].Url });
        VidForForgetPwd = response.data[1].Url;
      }
      else {
        this.setState({ Videos: response.data[0].Url });
        VidForForgetPwd = response.data[0].Url;
      }
    });
    // if ((localStorage.getItem("userinfo") == null) && (localStorage.getItem("loginDetail") !== null)) {
    //   let detail = localStorage.getItem("loginDetail");
    //   this.setState({ email: JSON.parse(detail).email, password: JSON.parse(detail).password, checkBoxStatus: true });
    //   // this.props.history.push("/warehouse/find");

    // }
  }
  handleChange(e) {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  }
  CloseModal() {
    this.setState({
      ShowverificationModal: false,
      Message: "",
      ShowforgotModal: false,
      ShowAlertModal: false,
      SuccessModal: false
    });
    this.resetForm();
  }
  resetForm() {
    initialState.Videos = VidForForgetPwd;
    this.setState(initialState);
  }
  ResendVerification() {
    this.setState({ ShowverificationModal: false, Message: "" });
    UserService.resendlink(this.state.Email).then(response => {
      if (response.data.Status) {
        this.setState({
          ShowverificationModal: true,
          Message:
            "A verification link has been sent to your email. Please verify your account."
        });
      }
    });
  }
  ForgotPassword() {
    this.setState({ ShowforgotModal: true, Message: "" });
  }
  RecoverPassword() {
    if (this.state.forgotEmail != "") {
      this.setState({ ShowforgotModal: false, Message: "" });
      UserService.SendforgotPassword(this.state.forgotEmail)
        .then(response => {
          if (response.data.Status) {
            this.setState({
              SuccessModal: true,
              Message: response.data.Message
            });
          } else {
            this.setState({
              ShowAlertModal: true,
              Message: response.data.Message
            });
          }
        })
        .catch(error => {
          alert(error);
        });
    }
  }
  handleKeyPress(e) {
    if (e.key === "Enter") {
      this.login();
    }
  }

  valid() {
    const errorObjects = Object.values(links).filter(x => !!x.error);
    if (errorObjects.length > 0) {
      const key = errorObjects.filter(x => !x.value).map(x => x.key)[0];
      alert(key)
      if (key) {
        this.setState({ [key]: undefined });
      }
      return false;
    } else {
      return true;
    }
  }
  checkBox() {
    if (!this.state.checkBoxStatus) {
      localStorage.removeItem('loginDetail');
      this.setState({ checkBoxStatus: !this.state.checkBoxStatus })
    } else {
      this.setState({ checkBoxStatus: !this.state.checkBoxStatus })
    }

  }
  login() {
    if (this.valid()) {
      var data = {
        email: this.state.Email,
        password: this.state.Password,
        type: this.state.Type
      };
      UserService.login(data)
        .then(response => {
          if (response.data.Status) {
            if (response.data.accessToken == "") {
              this.setState({
                ShowverificationModal: true,
                Message: response.data.Message
              });
            } else {
              localStorage.setItem("userinfo", response.data.accessToken);
              if (this.state.checkBoxStatus) {
                let loginDetail = {
                  email: this.state.Email,
                  password: this.state.Password
                }
                localStorage.setItem("loginDetail", JSON.stringify(loginDetail));
                let detail = localStorage.getItem("loginDetail");
                this.setState({ email: JSON.parse(detail).email, password: JSON.parse(detail).password, checkBoxStatus: true })
              }
              Set(response.data.accessToken);
              var UserData = Helper.DecodeToken(response.data.accessToken);
              if (UserData.Type == "Owner") {
                var date = new Date();
                var subscriptionDate = new Date(UserData.SubscriptionEndDate);

                if (date > subscriptionDate) {
                  this.props.history.push("/warehouse/list");
                  return;
                }
                if (UserData.Status == 1) {
                  this.props.history.push("/warehouse/list");
                  return;
                } else if (
                  UserData.Status == 3 &&
                  response.data.warehouseCount == 0
                ) {
                  this.props.history.push("/addressdetail");
                } else {
                  this.props.history.push("/warehouse/list");
                }
              }
              else if (localStorage.getItem("path") !== null) {
                var warehouseTypeCheck = JSON.parse(sessionStorage.getItem("ExtraWarehouseType"));
                var serviceCheck = JSON.parse(sessionStorage.getItem("ExtraServices"));
                var securityCheck = JSON.parse(sessionStorage.getItem("ExtraSecurity"));
                var other_one = sessionStorage.getItem("Otherserviceone");
                var other_two = sessionStorage.getItem("Otherservicetwo");
                this.setState({
                  ExtraWarehouseType: warehouseTypeCheck,
                  ExtraServices: serviceCheck,
                  ExtraSecurity: securityCheck,
                  Otherserviceone: other_one,
                  Otherservicetwo: other_two
                });
                if (this.state.ExtraWarehouseType || this.state.ExtraServices || this.state.ExtraSecurity || this.state.Otherserviceone || this.state.Otherservicetwo) {
                  UserService.extraservice(this.state)
                    .then(response => {
                      if (response.data.Status) {
                        // console.log("success");
                        // console.log(this.state);

                        sessionStorage.removeItem('ExtraServices');
                        sessionStorage.removeItem('ExtraSecurity');
                        sessionStorage.removeItem('ExtraWarehouseType');
                        sessionStorage.removeItem('Otherserviceone');
                        sessionStorage.removeItem('Otherservicetwo');

                        // this.props.history.push(localStorage.getItem("path"));
                        const path = localStorage.getItem("path"),
                          pathMatch = path.slice(0, 17);
                        if (pathMatch == '/warehouse/search') {
                          this.props.history.push(path);
                        }
                        localStorage.removeItem("path");
                        // console.log('localStorage.getItem("path")', localStorage.getItem("path"));
                      } else {
                        console.log("fail");
                        return 0;
                      }
                    })
                    .catch(error => {
                      console.log(error);
                      return 0;
                    });
                } else {
                  const path = localStorage.getItem("path"),
                  pathMatch = path.slice(0, 17);
                  if (pathMatch == '/warehouse/search') {
                    this.props.history.push(path);
                  }
                }
              }
              else {
                this.props.history.push("/warehouse/find");
                // console.log('localStorage.getItem("path")', localStorage.getItem("path"));
              }
            }
          } else {
            this.setState({
              ShowAlertModal: true,
              Message: response.data.Message
            });
          }
        })
        .catch(error => {
          alert(error);
        });
    }
  }
  render() {
    links = {
      emialLink: Link.state(this, "Email")
        .check(x => x, "Email Required")
        .check(x => EmailRegex.test(x), "Email not valid"),
      passwordLink: Link.state(this, "Password").check(
        x => x,
        "Password Required"
      )
    };

    const modalObject = {
      State: this.state,
      onChange: this.handleChange.bind(this),
      onClose: this.CloseModal.bind(this),
      onResend: this.ResendVerification.bind(this),
      recoverPasword: this.RecoverPassword.bind(this),
      SuccessModal: this.state.SuccessModal,

    };
    const loginObject = {
      State: this.state,
      ForgotPassword: this.ForgotPassword.bind(this),
      //onChange: this.handleChange.bind(this),
      onClick: this.login.bind(this),
      onKeyPress: this.handleKeyPress.bind(this),
      checkBox: this.checkBox.bind(this),
      links,
      path: this.props.location.pathname
    };

    return (
      <div>
        <Login {...loginObject} />
        <Modal {...modalObject} />
      </div>
    );
  }
}



