
import React from "react";

const Modal1 = props => {
  const ServiceModal = {
    ModalCss: props.State.ShowServiceModal ? "in" : "",
    ModalStyle: { display: props.State.ShowServiceModal ? "block" : "none" },
    ModalBody: props.State.Message,
    onClick: props.onClick,
    onClose: props.onClose,
    recoverPasword: props.recoverPasword
  };
  

  return (
    <div>
      <div class={"modal fade " + ServiceModal.ModalCss} id="myModal-forgot"  tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-forgot"
        style={ServiceModal.ModalStyle} >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot" style={{width:'146%'}}>
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={props.onClose} >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="formWarp password">

              <div class="container">
                <p></p>
                  <div class="table-responsive"  style={{borderStyle:'hidden'}} >
                    <table class="table borderless" style={{border: 'none !important'}}>
                      <thead>
                        <tr>
                          <th  style={{fontSize:'bold',textAlign:'center',paddingLeft:'35px'}}>Warehouse type</th>
                          <th></th>
                          
                          <th>Services</th>
                          <th></th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                            <td>
                                <label  class="" >
                            <input name="type" type="checkbox" id="Ambient" onClick= {props.WarehouseService}  />
                            
                                <span>
                               Ambient
                              </span>
                            </label>
                            </td>
                            <td>
                                  <label  class="" >
                              <input name="type" type="checkbox" id="Bonded" onChange= {props.WarehouseService}  />
                             
                                <span>
                               Bonded
                              </span>
                            </label>
                            </td>
                            <td>
                                  <label  class="" >
                              <input id="E-Commerce and Fulfillment"  onChange= {props.WarehouseService} name="service" type="checkbox" />
                             
                                <span>
                               E-Commerce and Fulfillment
                              </span>
                            </label>
                            </td>
                            <td>
                                  <label  class="" >
                              <input  id="Pallet storage" name="service" type="checkbox" onChange= {props.WarehouseService}  />
                                <span>
                               Pallet storage
                              </span>
                            </label>
                            </td>
                        </tr>
                        <tr>
                            <td >
                              <label  style={{display:'none'}}>Ambient</label>
                            </td>
                            <td>
                              <label  style={{display:'none'}}>Bonded</label>
                            </td>
                            <td>
                                  <label  class="" >
                              <input  name="service" id="Bulk Storage" type="checkbox" onChange={props.WarehouseService}  />
                                <span>
                               Bulk Storage
                              </span>
                            </label>
                            </td>
                            <td>
                                  <label  class="" >
                              <input  name="service" id="Import Export" type="checkbox" onChange={props.WarehouseService} />
                                <span>
                               Import Export
                              </span>
                            </label>
                            </td>
                        </tr>
                        <tr>
                          <th style={{fontSize:'bold',textAlign:'center',paddingLeft:'64px'}}>Security</th>
                            <td>
                                
                              <label style={{display:'none'}}>Bonded</label>
                            </td>
                            <td>
                                  <label  class="">
                              <input id="Container handling" name="service" type="checkbox" onChange= {props.WarehouseService} />
                                <span>
                               Container handling
                              </span>
                            </label>
                            </td>
                            <td>
                                  <label  class="" >
                              <input id="Home Delivery" name="service" type="checkbox" onChange={props.WarehouseService} />
                                <span>
                               Home Delivery
                              </span>
                            </label>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                  <label  class="" >
                              <input id="Cctv" name="security" type="checkbox" onChange= {props.WarehouseService}  />
                                <span>
                               Cctv
                              </span>
                            </label>
                            </td>
                            <td>
                                  <label  class="" >
                              <input id="24/7 surv" name="security" type="checkbox" onChange={props.WarehouseService} />
                                <span>
                               24/7 surv
                              </span>
                            </label>
                            </td>
                            <td>
                                  <label  class="" >
                              <input id="Pick and Pack" name="security" type="checkbox" onChange= {props.WarehouseService}  />
                                <span>
                               Pick and Pack
                              </span>
                            </label>
                            </td>
                            <td>
                                  <label  class="" >
                              <input id="Stock Management" name="security" type="checkbox" onChange= {props.WarehouseService} />
                                <span>
                               Stock Management
                              </span>
                            </label>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                  <label  class="" >
                              <input id="Fenced" name="security" type="checkbox" onChange= {props.WarehouseService} />
                                <span>
                               Fenced
                              </span>
                            </label>
                            </td>
                            <td>
                                  <label  class="" >
                              <input id="Alarmed" name="security" type="checkbox"  onChange= {props.WarehouseService} />
                                <span>
                               Alarmed
                              </span>
                            </label>
                            </td>
                            <td>
                                  <label  class="" >
                              <input id="Distribution" name="security" type="checkbox" onChange= {props.WarehouseService} />
                                <span>
                               Distribution
                              </span>
                            </label>
                            </td>
                            <td>
                                <label  class="" >
                              <input id="Management" name="security" type="checkbox" onChange={props.WarehouseService} />
                                <span>
                               Management
                              </span>
                            </label>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                  <label  class="" >
                              <input id="Secure Area" name="security" type="checkbox" onChange= {props.WarehouseService} />
                                <span>
                               Secure Area
                              </span>
                            </label>
                            </td>
                            <td>
                                <label  class="" >
                             <input id="other_extra1" name="checkbox" type="checkbox" placeholder="Please Specify Services" onClick={props.opentextboxone} />
                              <span>
                               Others
                              </span>
                            </label>
                             <input class="form-control" style={{display:'none'}} type="text" name="input_one" id="update_inputone"  onChange= {props.WarehouseService}/>
                            </td>
                             <td>
                                  <label  class="" >
                              <input id="Order Processing" name="security" type="checkbox"  onChange={props.WarehouseService} />
                                <span>
                               Order Processing
                              </span>
                            </label>
                            </td>
                            <td>
                                  <label  class="" >
                              <input id="Special Storage" name="security" type="checkbox" onChange= {props.WarehouseService}  />
                              <span>
                               Special Storage
                              </span>
                            </label>
                            </td>
                        </tr>
          
                        <tr>
                            <td>
                              <label style={{display:'none'}}>Ambient</label>
                            </td>
                            <td>
                              <label style={{display:'none'}}>Bonded</label>
                            </td>
                            <td>
                                <label  class="" >
                             <input id="other_extra2" name="checkbox" type="checkbox" placeholder="Please Specify Services" onClick= {props.opentextbox} />
                             <span>
                               Others
                              </span>
                            </label>
                             <input class="form-control" style={{display:'none'}}  type="text" name="input_two" id="update_inputtwo" onChange={props.WarehouseService}/>
                            </td>
                            <td>
                              <label style={{display:'none'}}>Special Storage</label>
                            </td>
                        </tr>
                      </tbody>
                    </table>
                  </div>

                </div>

                <input
                    type="button"
                    class="button button-border button-primary button-uppercase margin-top-3x"
                    value="Done"
                    onClick={props.onupdate}
                  /> 
              </div>
            </div>
          </div>
        </div>
      </div>
   
    </div>
  );
};
module.exports = Modal1;



