  getParamObject() {
    const search = { ...searchModel };
    search.Type = Helper.GetParamByName("ty");
    search.Location = Helper.GetParamByName("loc");
    search.AvailabilityFrom = Helper.GetParamByName("sd");
    search.AvailabilityTo = Helper.GetParamByName("ed");
    search.Distance = Helper.GetParamByName("ml");
    search.MaxHeightType = Helper.GetParamByName("ht");
    search.MaxHeight = Helper.GetParamByName("h");
    if (search.MaxHeightType != "Meter")
      search.MaxHeight = Formula.FeetToMeter(search.MaxHeight);

    search.MaxWeight = Helper.GetParamByName("w");
    search.MaxWeightType = Helper.GetParamByName("wt");
    if (search.MaxWeightType != "Kgs")
      search.MaxWeight = Formula.LbtoKg(search.MaxWeight);

    if (search.Type == "Pallet") {
      search.Pallet.Type.Name = Helper.GetParamByName("pt");

      search.Pallet.Total = Helper.GetParamByName("tp");
    } else {
      search.Bulk.Item = Helper.GetParamByName("bi");
      search.Bulk.ItemType = Helper.GetParamByName("bit");
      if (search.Bulk.ItemType != "Meter")
        search.Bulk.Item = Formula.FeetToMeter(search.Bulk.Item);

      search.Bulk.TotalItems = Helper.GetParamByName("tbi");
      search.Bulk.MaxLength = Helper.GetParamByName("bl");
      search.Bulk.MaxLengthType = Helper.GetParamByName("blt");
      if (search.Bulk.MaxLengthType != "Meter")
        search.Bulk.MaxLength = Formula.FeetToMeter(search.Bulk.MaxLength);

      search.Bulk.MaxWidth = Helper.GetParamByName("bw");
      search.Bulk.MaxWidthType = Helper.GetParamByName("bwt");
      if (search.Bulk.MaxWidthType != "Meter")
        search.Bulk.MaxWidth = Formula.FeetToMeter(search.Bulk.MaxWidth);

      search.TotalSpace = search.Bulk.Item * search.Bulk.TotalItems;
    }

    return search;
  }
