  ShowRequestPopup(id, header) {
    if (localStorage.getItem("userinfo") !== null) {
      let body = document.getElementsByTagName("BODY")[0];
      body.classList.add("modal-open");
      let user = Helper.DecodeToken(localStorage.getItem("userinfo"));
      let warehouseRequestState = { ...initialState };
      warehouseRequestState.WarehouseId = id
      warehouseRequestState.Name = user !== null && user.FirstName + " " + user.LastName;
      warehouseRequestState.Email = user !== null && user.Email;
      localStorage.removeItem('item_id');
      localStorage.removeItem('item_header');
      this.setState({ ShowRequestModal: true, PopUpHeader: header, WarehouseRequest: { ...warehouseRequestState } });     

      localStorage.setItem("userInfo", this.props.history.location.pathname + this.props.history.location.search);
    } else if (localStorage.getItem('item_id') !== null) {
      localStorage.removeItem('item_id');
      localStorage.removeItem('item_header');
    } else {
      // localStorage.removeItem("path")
      localStorage.setItem('item_id', id);
      localStorage.setItem('item_header', header);

      this.props.history.push('/findspace/register')
    }
  }

  RequestToRedirect(id) {

    const bookingLink = "/user/quotes?wid=" + id + "&type=" + Helper.GetParamByName("ty");
    // console.log(this.props.history, 'this.props.history');
    // this.props.history.location.pathname = "/user/quotes?wid=";
    this.props.history.location = bookingLink;
    // console.log(this.props.history);
    if (localStorage.getItem("userinfo") !== null) {
      this.props.history.push(bookingLink);
      localStorage.removeItem('bookindId');
    } else if (localStorage.getItem('bookindId')) {
      localStorage.removeItem('bookindId');
    } else {
      localStorage.setItem('bookindId', id);
      this.props.history.push('/findspace/register')
    }
  }

  valid() {
    console.log(Object.values(links),'************link Object');
    
    const errorObjects = Object.values(links).filter(x => !!x.error);
    console.log(errorObjects,'************Object.values(links)');
    if (errorObjects.length) {
      const key = errorObjects.filter(x => !x.value).map(x => x.key)[0];
      if (key) {
        this.setState({ [key]: undefined });
      }
      return false;
    } else return true;
  }
  sendMessage() {

    localStorage.removeItem('path')
    if (this.valid()) {
      this.state.WarehouseRequest.Type = this.state.PopUpHeader;
      WarehouseService.sendMessage(this.state.WarehouseRequest).then(response => {
        if (response.data.Status) {
          this.setState({
            RequestSuccessModal: true,
            ShowRequestModal: false,
            ModalBody: response.data.Message,
            WarehouseRequest: { ...initialState }
          });
        } else {
          this.setState({
            RequestAlertModal: true,
            ShowRequestModal: false,
            ModalBody: response.data.Message,
            WarehouseRequest: { ...initialState }
          });
        }
      });
    }
  }
