import React from "react";
import VInput from "Components/common/VInput";
import Helper from "../../../helpers/utility.helper.js";
const Pellet = props => {
  let k = "";
  let val = "";
  let cost = 0;

  let warehouse = props.State.warehouse.map((val, k) => (
    <div class='row Row-10'> <div class='costBreak'> <div class='form-group row'> <label for='example-text-input' class='col-2 col-form-label'>{val} </label><div class='col-10 input-group'><span class='input-group-addon' id='basic-addon1'>&pound;</span> <input type='text' onChange={props.ServiceStorageCost} class='form-control' name={val} aria-describedby='basic-addon1' /></div></div></div></div>
  ));

  let services = props.State.services.map((val, k) => (
    <div class='row Row-10'> <div class='costBreak'> <div class='form-group row'> <label for='example-text-input' class='col-2 col-form-label'>{val} </label><div class='col-10 input-group'><span class='input-group-addon' id='basic-addon1'>&pound;</span> <input type='text' onChange={props.ServiceStorageCost} class='form-control' name={val} aria-describedby='basic-addon1' /></div></div></div></div>
  ));

  let security = props.State.security.map((val, k) => (
    <div class='row Row-10'> <div class='costBreak'> <div class='form-group row'> <label for='example-text-input' class='col-2 col-form-label'>{val} </label><div class='col-10 input-group'><span class='input-group-addon' id='basic-addon1'>&pound;</span> <input type='text' onChange={props.ServiceStorageCost} class='form-control' name={val} aria-describedby='basic-addon1' /></div></div></div></div>
  ));

  let OtherServiceOne = "";
  if (props.State.OtherServiceOne == 1) {
    OtherServiceOne = (
      <div class='row Row-10'> <div class='costBreak'> <div class='form-group row'> <label for='example-text-input' class='col-2 col-form-label'>{props.State.OtherOne} </label><div class='col-10 input-group'><span class='input-group-addon' id='basic-addon1'>&pound;</span> <input type='text' onChange={props.ServiceStorageCost} class='form-control' name={props.State.OtherOne} aria-describedby='basic-addon1' /></div></div></div></div>);
  }
  let OtherServicetwo = "";
  if (props.State.OtherserviceTwo == 1) {
    OtherServicetwo = (
      <div class='row Row-10'> <div class='costBreak'> <div class='form-group row'> <label for='example-text-input' class='col-2 col-form-label'>{props.State.OtherTwo} </label><div class='col-10 input-group'><span class='input-group-addon' id='basic-addon1'>&pound;</span> <input type='text' onChange={props.ServiceStorageCost} class='form-control' name={props.State.OtherTwo} aria-describedby='basic-addon1' /></div></div></div></div>);
  }
  return (
    <form>
      <div id="PalletStorage" class="open-form" style={{ display: "block" }}>
        <div class="row Row-10">

          <div class="col-xs-12 col-sm-6 form-group">
            <label>Space Available</label>
            <input
              type="text"
              class="form-control"
              placeholder=""
              value={
                parseFloat(props.State.TotalSpace).toFixed(2) +
                " sq" +
                props.State.WarehouseDetail.CapacityType
              }
              readOnly="readOnly"
            />
          </div>
          <div class="col-xs-12 col-sm-6 form-group">
            <label>Time Available</label>
            <input
              type="text"
              class="form-control"
              placeholder=""
              value={
                Helper.ConvertDateToString(
                  props.State.WarehouseDetail.Pallet.AvailabilityFrom
                ) +
                " - " +
                Helper.SetEndDate(
                  props.State.WarehouseDetail.Pallet.AvailabilityTo
                )
              }
              readOnly="readOnly"
            />
          </div>
        </div>
        <div class="row Row-10">
          <div class="col-xs-12 col-sm-6 form-group">
            <label>Type of Pallets</label>
            <input
              type="text"
              class="form-control"
              placeholder=""
              value={props.State.WarehouseDetail.Pallet.Type.Name}
              readOnly="readOnly"
            />
          </div>
          <div class="col-xs-12 col-sm-6 form-group">
            <label>Number of Pallets</label>
            <input
              type="text"
              class="form-control"
              placeholder=""
              value={props.State.Pallet.TotalPallets}
              readOnly="readOnly"
            />
          </div>
        </div>

        <div class="row Row-10">
          <div class="col-xs-12 form-group">
            <label>Load Description</label>
            <textarea
              class="form-control"
              placeholder=""
              readOnly="readOnly"
              value={props.State.Pallet.Description}
            />
          </div>
        </div>
        <div class="row Row-10">
          <div class="col-xs-12 col-sm-6 form-group">
            <label>Height (Meters/feet)</label>
            <input
              type="text"
              class="form-control"
              placeholder=""
              value={props.State.Pallet.HeigthPerPellet}
              readOnly="readOnly"
            />
          </div>
          <div class="col-xs-12 form-group">
            <label>Industrial Category</label>
            <input
              type="text"
              class="form-control"
              placeholder=""
              value={props.State.Pallet.Category}
              readOnly="readOnly"
            />
          </div>
        </div>
        <div class="row Row-10">
          <div class="col-xs-12 col-sm-6 form-group">
            <label>From (Start date)</label>
            <div class="has-feedback">
              <input
                type="text"
                class="form-control"
                placeholder=""
                value={props.State.Pallet.FromDate}
                readOnly="readOnly"
              />
              <span class="form-control-feedback">
                <i class="fa fa-calendar" />
              </span>
            </div>
          </div>
          <div class="col-xs-12 col-sm-6 form-group">
            <label>To (End Date)</label>
            <div class="has-feedback">
              <input
                type="text"
                class="form-control"
                placeholder=""
                value={props.State.Pallet.ToDate}
                readOnly="readOnly"
              />
              <span class="form-control-feedback">
                <i class="fa fa-calendar" />
              </span>
            </div>
          </div>
        </div>
        <div class="row Row-10">
          <div class="costBreak">
            <label style={{ fontSize: '18px', borderBottom: '2px solid #39b449' }} >Storage Breakdown</label>

            <div class="form-group row">
              <label for="example-text-input" class="col-2 col-form-label">
                Handling in
              </label>
              <div class="col-10 input-group">
                <span class="input-group-addon" id="basic-addon1">
                  &pound;
                </span>
                <VInput
                  type="text"
                  class="form-control"
                  name="HandlingIn"
                  placeholder=""
                  valueLink={props.palletlinks.handlingInLink}
                  onChange={props.onPalletPriceChange}
                  aria-describedby="basic-addon1"
                />
              </div>
            </div>
            <div class="form-group row">
              <label for="example-text-input" class="col-2 col-form-label">
                Handling Out
              </label>
              <div class="col-10 input-group">
                <span class="input-group-addon" id="basic-addon1">
                  &pound;
                </span>
                <VInput
                  type="text"
                  class="form-control"
                  name="HandlingOut"
                  placeholder=""
                  valueLink={props.palletlinks.handlingOutLink}
                  onChange={props.onPalletPriceChange}
                  aria-describedby="basic-addon1"
                />
              </div>
            </div>
            <div class="form-group row">
              <label for="example-text-input" class="col-2 col-form-label" >
                Storage
              </label>
              <div class="col-10 input-group">
                <span class="input-group-addon" id="basic-addon1">
                  &pound;
                </span>
                <VInput
                  type="text"
                  class="form-control"
                  placeholder=""
                  name="Storage"
                  valueLink={props.palletlinks.storageLink}
                  onChange={props.onPalletPriceChange}
                  aria-describedby="basic-addon1"
                />
              </div>
            </div>
          </div>
        </div>

        <div class="row Row-10">
          <div class="costBreak">
            <div class="form-group row">
              <label for="example-text-input" class="col-2 col-form-label">
                Storage Final Cost
              </label>
              <div class="col-10 input-group">
                <span class="input-group-addon" id="basic-addon1">
                  &pound;
                </span>
                <VInput
                  type="text"
                  class="form-control"
                  placeholder=""
                  name="FinalCost"
                  readOnly="true"
                  valueLink={props.palletlinks.FinalCostLink}
                  aria-describedby="basic-addon1"
                />
              </div>
            </div>
          </div>
        </div>
        <label id="show_title" style={{ fontWeight: 'bold', marginBottom: '17px', fontSize: '20px', borderBottom: '2px solid #39b449' }} class="h2Title" >Preferred Additional Service</label>

        <div id="CheckBoxData">
          {warehouse}
          {services}
          {security}
          {OtherServiceOne}
          {OtherServicetwo}
        </div>

        <div class="row Row-10">
          <div class="costBreak">
            <div class="form-group row">
              <label for="example-text-input" class="col-2 col-form-label">
                Services Final Cost
              </label>
              <div class="col-10 input-group">
                <span class="input-group-addon" id="basic-addon1">
                  &pound;
                </span>
                <input
                  type="text"
                  class="form-control"
                  placeholder=""
                  name="ServicesFinalCost"
                  readOnly="true"
                  value={props.State.TotalServicesCost}
                  aria-describedby="basic-addon1"
                />
              </div>
            </div>
          </div>
        </div>
        <div class="row Row-10">
          <div
            class="col-xs-12 col-sm-6 form-group"
            style={{ marginTop: "10px" }}
          >
            <div class="form-group return">
              <button
                type="button"
                class="button button-primary"
                onClick={props.submitQuote}
              >Submit to client</button>
            </div>
          </div>
        </div>
      </div>
    </form>
  );
};
module.exports = Pellet;



