import React, { Component } from "react";
import WarehouseService from "Services/warehouse.service.js";
import Find from "./find.js";
import Helper from "../../../helpers/utility.helper.js";
import canUseDom from "can-use-dom";
import { http } from "Helpers/axios.helper";

const searchModel = {
  Type: "Pallet",
  Location: "",
  Distance: "10",
  Startdate: "",
  EndDate: "",
  Lat: "0",
  Lng: "0"
};
let myMap, markers = [];
let geolocation = null;
if (canUseDom && navigator.geolocation) geolocation = navigator.geolocation;

export default class findContainer extends Component {
  constructor() {
    super();
    this.state = {
      search: { ...searchModel },
      center: {
        lat: 51.509865,
        lng: -0.118092
      },
      places: [],
      map: "",
      warehouseIds: [],
      totalRecords: -1
    };

    this.onChange = this.onChange.bind(this);
    this.onflatpicker = this.onflatpicker.bind(this);
    this.onNext = this.onNext.bind(this);
    this.onSearch = this.onSearch.bind(this);
    this.fillData = this.fillData.bind(this);
    this.handleKeyPress = this.handleKeyPress.bind(this);
    this.handleOnFocusOut = this.handleOnFocusOut.bind(this);
  }
  onChange(e) {
    if (e.preventDefault()) e.preventDefault();

    let { name, value, text } = e.target;
    if (!value) value = text;
    this.setState({
      search: {
        ...this.state.search,
        [name]: value
      }
    });
  }
  redirect() {
    this.props.history.push('/contact-us');
  }

  onflatpicker(name, value) {
    this.setState({
      search: { ...this.state.search, [name]: value }
    });
  }
  handleKeyPress(e) {
    this.onSearch();
    // if (e.key === "Enter") {
    //   this.onSearch();
    // }
  }
  handleOnFocusOut() {
    console.log('******handleOnFocusOut******');
    
    if (geolocation)
      geolocation.getCurrentPosition(
        position => {
          this.setState({
            center: {
              lat: position.coords.latitude,
              lng: position.coords.longitude
            }
          });
        },
        error => { }
      );

    // var google = new google.maps.place
    // console.log("autocomplete", this.autocomplete);

    // this.autocomplete = new google.maps.places.Autocomplete(
    //   this.locationAutocomplete,
    //   { types: ["geocode"] }
    // );
    // google.maps.event.clearInstanceListeners(this.autocomplete);
    // this.autocomplete.addListener("place_changed", this.fillData);
  }
  
  onSearch() {
    if (
      !this.state.search.Location ||
      this.state.search.Location == "" ||
      !this.state.search.Lat ||
      this.state.search.Lat == "0" ||
      !this.state.search.Lng ||
      this.state.search.Lng == "0"
    ) {
      return false;
    }
    WarehouseService.Find(this.state.search)
      .then(response => {
        if(markers && markers.length > 0) {
          markers.forEach(element => {
            element.setMap(null);
          });
          markers.length = 0;
        } 
        const places = response.data.map(x => x.Coordinates);
        const warehouseIds = response.data.map(x => x.warehouseId);
        const totalRec = places.length;
        // + " warehouse found";
        this.setState({ places, warehouseIds, totalRecords: totalRec });
        // Create an array of alphabetical characters used to label the markers.
        // var labels = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
        
        this.state.places.forEach(location => {
          let latlong = {
            lat: location[0],
            lng: location[1]
          }
          let marker = new google.maps.Marker({
            position: latlong,
            map: myMap,
            // label: labels[i % labels.length]
          });
          markers.push(marker);
        });
        return markers;
      })
      .catch(err => {
        console.log(err);
      });
  }

  componentWillMount() {
    // if (localStorage.getItem("userinfo") != null) {
    //   let UserDetail = Helper.DecodeToken(localStorage.getItem("userinfo"));

    //   if (UserDetail.Type == "Buyer") {
    //     if (localStorage.getItem("path") !== null) {
    //       const path = localStorage.getItem("path"),
    //         pathMatch = path.slice(0, 17);
    //       if (pathMatch == '/warehouse/search') {
    //         this.props.history.push(path);
    //       }
    //     } else {
    //       this.props.history.push("/warehouse/find");
    //     }
    //   }
    // }
    const search = {
      Type: !Helper.GetParamByName("ty")
        ? "Pallet"
        : Helper.GetParamByName("ty"),
      Distance: !Helper.GetParamByName("ml")
        ? "5"
        : Helper.GetParamByName("ml"),
      Location: Helper.GetParamByName("lo"),
      Startdate: Helper.GetParamByName("sd"),
      EndDate: Helper.GetParamByName("ed")
    };
    this.setState({
      search: { ...search }
    });
  }

  componentDidMount() {
    if (geolocation)
      geolocation.getCurrentPosition(
        position => {
          this.setState({
            center: {
              lat: position.coords.latitude,
              lng: position.coords.longitude
            }
          });
        },
        error => { }
      );

    // var google = new google.maps.place
    // console.log("autocomplete", this.autocomplete);
    myMap = new google.maps.Map(document.getElementById('map'), {
      center: {lat: this.state.center.lat, lng: this.state.center.lng},
      zoom: 10
    })    
    this.autocomplete = new google.maps.places.Autocomplete(
      this.locationAutocomplete,
      { types: ["geocode"] }
    );
    google.maps.event.clearInstanceListeners(this.autocomplete);
    this.autocomplete.addListener("place_changed", this.fillData);
  }

  fillData() {
    const place = this.autocomplete.getPlace();
    const location = {
      lat: place.geometry.location.lat(),
      lng: place.geometry.location.lng()
    };
    myMap = new google.maps.Map(document.getElementById('map'), {
      center: {lat: location.lat, lng: location.lng},
      zoom: 10
    }) 
    this.setState({
      center: location,
      search: {
        ...this.state.search,
        Location: place.formatted_address,
        Lat: location.lat,
        Lng: location.lng
      }
    });
  }

  onNext() {
    if (this.state.warehouseIds.length == 0) {
      return false;
    }
    const queryString =
      "?ty=" +
      this.state.search.Type +
      "&loc=" +
      this.state.warehouseIds.join() +
      "&sd=" +
      this.state.search.Startdate +
      "&ed=" +
      this.state.search.EndDate +
      "&ml=" +
      this.state.search.Distance;
    window.history.pushState(
      {},
      "",
      window.location.origin + window.location.pathname + queryString
    );
    this.props.history.push("/warehouse/finddetails" + queryString);
  }

  render() {

    const model = {
      model: this.state,
      search: this.onSearch,
      next: this.onNext,
      change: this.onChange,
      center: this.state.center,
      onflatpicker: this.onflatpicker,
      setRef: el => (this.locationAutocomplete = el),
      places: this.state.places,
      keyPress: this.handleKeyPress,
      onfocusout: this.handleOnFocusOut,
      redirect: this.redirect.bind(this)
    };

    return <Find {...model} />;
  }
}



