import React from "react";
import { Input } from "valuelink/tags";

let inputName = {};

export default props => {
  // console.log(props, 'props**')
  const { valueLink, ...others } = props;
  let hasError = !!valueLink.error;

  if (
    inputName[others.name] == undefined ||
    (valueLink.value == inputName[others.name].value &&
      !inputName[others.name].error)
  ) {
    hasError = false;
  }

  inputName[others.name] = {
    value: valueLink.value,
    error: hasError
  };
  let inputeValue = props.inputeValue !== undefined ? props.inputeValue : '';
  props.valueLink.value = props.valueLink.value || inputeValue;

  const hasOnChange = !!props.onChange;
  const { onChange, ...otherWithoutOnChange } = others;
  return (
    <div>
      {hasOnChange
        ? <input
          {...otherWithoutOnChange}
          onChange={onChange}
          value={valueLink.value}
        />
        : <Input {...props} />}
      {hasError &&
        <p class="text-danger">
          {valueLink.error}
        </p>}
    </div>
  );
};



